.input{
    background-color: white;
    height: 50px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.input input{
    width: 100%;
    border: none;
    outline: none;
    color: #2f2d52;
    font-size: 18px;
}
.input input::placeholder{
    color: lightgrey;
}
.send{
    display: flex;
    align-items: center;
    gap: 10px;
}
.send img{
    height: 24px;
    cursor: pointer;
}
.send button{
    border: none;
    padding: 10px 15px;
    color: white;
    background-color: #8da4f1;
    cursor: pointer;
}